@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  overflow-x: hidden;
}

nav {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100vw;
  padding: 1rem 2rem;
  margin-bottom: 2.5rem;
  box-shadow: 0px 2.98px 7.45px rgba(0, 0, 0, 0.1);
}

.logo {
  width: 150px;
}

/*Hero section*/
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1rem 5vw;
}

.hero-img {
  width: 95%;
  margin: 0 2.5%;
}

.hero-header {
  font-size: 3rem;
  font-weight: 600;
  letter-spacing: 1;
  padding: 0;
  margin: 4rem 0 0.75rem 0;
}

.hero-text {
  font-size: 1.25rem;
  width: 425px;
  color: rgba(50, 50, 50, 1);
  margin: 0;
}

/*Card Section*/

.card-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5rem 0;
  padding: 0;
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 200px;
  margin-right: 50px;
}

.card-img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  border-radius: 10px;
}

.card-stats {
  display: flex;
  align-items: center;
}

.card-star {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.card-stats > h3 {
  font-weight: 400;
  font-size: 1rem;
  color: rgba(100, 100, 100, 1);
  margin: 0.5rem 0;
}

.card-title {
  color: rgba(50, 50, 50, 1);
  font-size: 1rem;
  font-weight: 400;
  margin: 0.4rem 0;
}

.card > h2 {
  font-size: 1.1rem;
  font-weight: 400;
  margin: 0.35rem 0;
  color: rgba(50, 50, 50, 1);
}

.card > h2 > span {
  font-weight: 600;
  color: rgba(0, 0, 0, 1);
}

.card-badge {
  position: absolute;
  top: 6px;
  left: 6px;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 5px 7px;
  border-radius: 5px;
  font-weight: bold;
}
